   margin: 10px;\n    padding: 10px;\n    border: 2px solid var(--text-color);\n    border-radius: 2px;\n    box-shadow: inset 0 2px 4px var(--shadow-color);\n    width: min(100%, 300px);\n  }\n}\n\n.opponent-setup {\n  display: none;\n}\n\n.create-players {\n  margin-top: 20px;\n}\n\nbutton {\n  padding: 0.8em 1.5em;\n  cursor: pointer;\n  border: 2px solid var(--text-color);\n  background-color: var(--bg-color);\n  color: var(--text-color);\n  box-shadow: 0 2px 4px var(--shadow-color);\n  font-weight: 700;\n\n  &:hover {\n    background-color: var(--text-color);\n    color: var(--bg-color);\n  }\n\n  &:active {\n    transform: translateY(2px);\n  }\n\n  &:disabled {\n    opacity: 0.5;\n    cursor: not-allowed;\n\n    &:hover {\n      background-color: var(--bg-color);\n      color: var(--text-color);\n    }\n\n    &:active {\n      transform: none;\n    }\n  }\n}\n\n.game-guidance {\n  background-color: var(--accent-color);\n  padding: 0.5em 1em;\n  margin-bottom: 30px;\n}\n\n.ship-placing-area {\n  display: flex;\n\n  .unplaced-ships {\n    margin-right: 40px;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    width: 235px;\n\n    .unplaced-ship {\n      margin: 5px 0 5px 0;\n      border: 2px solid var(--text-color);\n      background-color: var(--shadow-color);\n      height: var(--cell-size);\n      width: var(--cell-size);\n\n      &.hidden {\n        visibility: hidden;\n      }\n    }\n  }\n}\n\n.placing-buttons {\n  display: flex;\n  justify-content: center;\n  margin-top: 30px;\n  gap: 10px;\n}\n\n.dragged-ship {\n  display: none;\n  position: absolute;\n  border: 2px solid var(--text-color);\n  background-color: var(--shadow-color);\n  height: var(--cell-size);\n  width: var(--cell-size);\n\n  &.dragging {\n    display: block;\n  }\n}\n\n.gameboards {\n  display: flex;\n  gap: 40px;\n\n  .player-area,\n  .opponent-area {\n    display: grid;\n    gap: 10px;\n    grid-template-columns: auto auto;\n\n    h1 {\n      grid-column: 2 / 3;\n      grid-row: 1 / 2;\n      display: flex;\n      justify-content: center;\n    }\n\n    .ship-status {\n      grid-column: 1 / 2;\n      grid-row: 2 / 3;\n      position: relative;\n      top: calc(var(--cell-size));\n\n      .ship {\n        display: flex;\n        margin: 3px 0;\n\n        .cell {\n          height: calc(var(--cell-size) / 5);\n          width: calc(var(--cell-size) / 5);\n          background-color: var(--ship-color);\n          margin: 1px;\n        }\n\n        &.sunk {\n          .cell {\n            background-color: var(--hit-color);\n          }\n        }\n      }\n    }\n\n    .gameboard {\n      grid-column: 2 / 3;\n      grid-row: 2 / 3;\n    }\n  }\n\n  .opponent-area {\n    h1 {\n      grid-column: 1 / 2;\n      grid-row: 1 / 2;\n    }\n\n    .gameboard {\n      grid-column: 1 / 2;\n      grid-row: 2 / 3;\n    }\n\n    .ship-status {\n      grid-column: 2 / 3;\n      grid-row: 2 / 3;\n    }\n  }\n\n  .player-area.inactive,\n  .opponent-area.inactive {\n    .ship-status,\n    .gameboard {\n      opacity: 0.5;\n    }\n  }\n}\n\n.pause-game {\n  margin-top: 30px;\n}\n\n.pause.modal > .modal-content,\n.game-over.modal > .modal-content {\n  display: flex;\n  flex-direction: column;\n  gap: 20px;\n\n  .game-winner,\n  .modal-title {\n    display: flex;\n    justify-content: center;\n  }\n\n  .modal-buttons {\n    display: flex;\n    gap: 10px;\n  }\n}\n\n.gameboard {\n  grid-template-columns: repeat(10, var(--cell-size));\n  grid-template-rows: repeat(10, var(--cell-size));\n  position: relative;\n\n  .gameboard-row {\n    display: flex;\n    flex-direction: row;\n  }\n\n  .header-cell {\n    font-family: 'Bebas Neue', serif;\n    font-size: 1.5rem;\n    font-weight: 500;\n    height: var(--cell-size);\n    width: var(--cell-size);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin: -1px 0 0 -1px;\n    user-select: none;\n  }\n\n  .gameboard-cell {\n    height: var(--cell-size);\n    width: var(--cell-size);\n    font-size: 1.5rem;\n    border: 1px solid var(--text-color);\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    margin: -1px 0 0 -1px;\n\n    &.adjacent {\n      background-color: var(--adjacent-color);\n    }\n\n    &.ship {\n      background-color: var(--ship-color);\n    }\n\n    &.miss {\n      background-color: var(--miss-color);\n\n      &::before {\n        content: '';\n      }\n    }\n\n    &.hit {\n      background-color: var(--hit-color);\n\n      &::before {\n        content: 'X';\n      }\n    }\n\n    &.sunk {\n      background-color: var(--sunk-color);\n    }\n  }\n\n  .placed-ship {\n    position: absolute;\n    border: 2px solid var(--text-color);\n    height: calc(var(--cell-size));\n    width: calc(var(--cell-size));\n\n    &.hidden {\n      display: none;\n    }\n  }\n\n  .ship-placement-indicator {\n    display: none;\n    position: absolute;\n    height: calc(var(--cell-size));\n    width: calc(var(--cell-size));\n    border: 2px solid var(--ship-placement-border-color);\n    background-color: var(--ship-placement-bg-color);\n\n    &.active {\n      display: block;\n    }\n  }\n}\n\n.opponent-area {\n  .gameboard-cell {\n    &:not(.hit):not(.miss):not(.adjacent):hover::before {\n      content: 'X';\n    }\n\n    &:not(.hit):not(.miss):not(.adjacent):hover {\n      cursor: pointer;\n    }\n  }\n}\n\n.opponent-area {\n  .gameboard-cell {\n    &.adjacent:not(.hit):not(.miss)::before {\n      content: '';\n      color: var(--ship-color);\n    }\n  }\n}\n\n.modal {\n  display: none;\n  z-index: 2;\n  position: fixed;\n  left: 0;\n  top: 0;\n  width: 100vw;\n  height: 100vh;\n  background-color: var(--modal-bg-color);\n  align-items: center;\n  justify-content: center;\n\n  .modal-content {\n    z-index: 3;\n    background-color: var(--bg-color);\n    padding: 16px;\n    width: auto;\n  }\n}\n\n.pass-turn.modal {\n  background-color: var(--bg-color);\n  display: none;\n}\n\n@media (max-width: 1200px) {\n  .gameboards {\n    flex-direction: column;\n\n    .player-area,\n    .opponent-area {\n      grid-template-columns: auto 1fr;\n      .gameboard {\n        grid-column: 1 / 3;\n        grid-row: 2 / 3;\n      }\n\n      .ship-status {\n        grid-column: 1 / 2;\n        grid-row: 1 / 2;\n        top: 0;\n      }\n\n      h1 {\n        grid-column: 2 / 3;\n        grid-row: 1 / 2;\n        align-items: center;\n      }\n    }\n  }\n}\n\n@media (max-width: 900px) {\n  .ship-placing-area {\n    flex-direction: column;\n    align-items: center;\n\n    .unplaced-ships {\n      width: 70%;\n      display: flex;\n      gap: 10px;\n      flex-direction: row;\n      justify-content: start;\n      flex-wrap: wrap;\n      margin-right: 0;\n    }\n  }\n\n  .placing-buttons {\n    flex-direction: column;\n  }\n}\n\nfooter {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n}\n\n#gh-link {\n  font-family: 'Bebas Neue', serif;\n  text-decoration: none;\n  font-size: 1.5rem;\n  font-weight: 500;\n  color: var(--text-color);\n  letter-spacing: 0;\n  display: flex;\n  align-items: center;\n  margin: 20px 0 10px 0;\n  height: 38px;\n}\n\n#gh-link > div:first-child {\n  width: 130.88px;\n  text-align: end;\n}\n\n#gh-logo {\n  width: 35px;\n  height: 35px;\n  margin: 0 10px;\n  transition: transform 0.3s ease-in-out;\n}\n\n#gh-link:hover #gh-logo {\n  transform: rotate(360deg) scale(1.2);\n}\n\nhtml {\n  visibility: visible;\n  opacity: 1;\n}\n"],"sourceRoot":""}]);
